// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

//element ui
import ElementUI from 'element-ui'
// import enLocale from 'element-ui/lib/locale/lang/en'
// import zhLocale from 'element-ui/lib/locale/lang/zh-CN'
// import twLocale from 'element-ui/lib/locale/lang/zh-TW'
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

//
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import axios from 'axios' //axios
axios.defaults.baseURL = 'https://wd5203671990lmrgyr.wilddogio.com/' //
Vue.prototype.$axios = axios //Vue,axios


import animate from 'animate.css'

import i18n from './i18n/i18n'; //


import moment from 'moment'
//
Vue.filter('moment', function(value, formatString) {
    console.log(formatString)
    formatString = formatString || 'YYYY-MM-DD HH:mm:ss';
    return moment(value).format("YYYY-MM-DD"); // value 20170723
});

// echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts



//vuex
import { store } from './store/strore.js'

//
import { routes } from './routers.js'

const router = new VueRouter({
    // mode: 'history',
    //	history
    routes
})


//
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requireLogin)) { // 
        if (sessionStorage.getItem('login')) { // loginInfo
            next();
        } else {
            Vue.prototype.$message({
                type: 'error',
                message: '!'
            });
            next({
                path: '/login'
            })
        }
    } else {
        next();
    }

})



/* eslint-disable no-new */
new Vue({
    router,
    store,
    i18n, //VueI18nvue

    el: '#app',
    components: { App },
    template: '<App/>'
})


